export default function (Component) {
       if(!Component.options.wxsCallMethods){
         Component.options.wxsCallMethods = []
       }
       Component.options.wxsCallMethods.push('enter')
Component.options.wxsCallMethods.push('doEnter')
Component.options.wxsCallMethods.push('doEnterTo')
Component.options.wxsCallMethods.push('leave')
Component.options.wxsCallMethods.push('doLeave')
Component.options.wxsCallMethods.push('doLeaveTo')
     }